<template>
  <div class="comm">
    <div style="padding-top:0.5rem;">
      <div class="comments">
        <div class="comment">
          <div class="bCom">
            <img class="img" :src="staUrl+activityAvatar">
          </div>
          <div class="rCom">
            <p style="color:#2399bf">{{obj.author.nickname}}</p>
            <p class="">{{obj.content}}</p>
            <p class="wsp">
              <span>{{obj.commentCount}}回复</span>
              <span>{{obj.created | fmtDate('yyyy-MM-dd')}}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="comments"  v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10">
        <div class="comment" v-for="(r,index) in reviews">
          <div class="bCom">
            <img class="img" :src="staUrl+activityAvatar">
          </div>
          <div class="rCom">
            <p style="color:#2399bf">{{r.author.nickname}}</p>
            <p class="sp">{{r.content}}</p>
            <p class="wsp">
              <span>{{r.created | fmtDate('yyyy-MM-dd')}}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as config from '../config/config.js';
  import { InfiniteScroll } from 'mint-ui';
  export default{
    name:'comm',
    data(){
      return {
        staUrl:config.IMG_BASE_URL,
        obj:{
          author:{
            activityAvatar:'',
            nickname:''
          }
        },
        reviews:[],
        page:1,
        loading:false,
        activityAvatar:''
      }
    },
    created(){
      let id = this.$route.params.cid;
      let url1 = config.API_BASE_URL+'/post/review/'+id;
      this.axios.get(url1)
      .then((res)=>{
        this.obj = res.data.review;
        this.activityAvatar = this.obj.author.activityAvatar;
      })
      .catch((error)=>{
        console.log(error);
      });

      this.getComments();
    },
    methods:{
      loadMore(){
        this.loading = true;
        setTimeout(() => {
          this.page++;
          this.getComments();
          this.loading = false;
        }, 1500);
      },
      getComments(){
        let id = this.$route.params.cid;
        let url2 = config.API_BASE_URL+'/post/review/'+id+'/comment?'+'start='+ (this.page-1)*20+'&limit=20';
        this.axios.get(url2)
        .then((res)=>{
          for (let [index,b] of res.data.comments.entries()){
            this.reviews.push(b);
          }
        })
        .catch((error)=>{
          console.log(error);
        });
      }
    }
  }
</script>
<style lang="less" scoped>
  .comm {
    margin-top: 2.5rem;
        overflow: hidden;
  }
.comments{
  text-align: left;
}
  .comment {
  clear:both;
  overflow: hidden;
}
.bCom{
  float: left;
  padding-left: 1rem;
  width: 18%;
}
.rCom {
  float: right;
  font-size: 14px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 1rem;
  width: 75%;
}
.rCom p{
  width: 226px;
  word-break: break-all;
}
.img {
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
}
.detail {
  background-color:#ddd;
  font-family: SimSun;
}
.wsp {
  font-size: 12px;
  color:#666;
  padding-bottom: 0.5rem;
  span:first-child{
    padding-right: 0.5rem;
  }
}
</style>


